.node-editor {
  position: fixed;
  top: 0;
  left: 0;
  height: 100vh;
  width: 300px;
  background: #5b999b;
  z-index: 999;

  &__content {
    display: flex;
    flex-direction: column;
    row-gap: 25px;
    margin: 50px 20px 0;

    input {
      padding-left: 10px;
      border: none; }

    .input__label {
      height: 25px; }


    .input__content {
      min-height: 100px;
      display: block; } }


  &__item {
    display: flex;
    flex-direction: column;
    row-gap: 12px; }

  &-submit {
    background: #ffffff;
    font-size: 18px;
    padding: 10px 0;
    cursor: pointer; } }

textarea {
  resize: none; }
